<template>
    <div class="nearBy">
        <div id="nearBy"></div>
        <header>
            <el-form  class="ordered-info" >
                <el-form-item>
                    <el-input style="width:200px" placeholder="请输入您想预约的停车场"></el-input>
                </el-form-item>
                <el-form-item >
                    <el-button type="primary" @click="search()">立即搜索</el-button>
                </el-form-item>
                <el-form-item  v-show="isSearch==true">
                    <div class="parkspace">
                        <el-row :gutter="20">
                            <el-col :span="6" v-for="(item,index) in parkspace" :index="index">
                                <div class="grid-content">
                                    <el-tooltip class="item" effect="dark" :content="item.content" placement="top">
                                        <i class="icon":class="item.icon" @click="order()"></i>
                                    </el-tooltip>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </el-form-item>
            </el-form>
        </header>
        <el-dialog title="提示" :visible.sync="orderDialogVisible">
            <span>确认预定</span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="orderDialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    <footer-bar class="footer"></footer-bar>
    </div>
</template>

<script>
    import footerBar from '../common/footerBar'
    export default {
        name: "nearBy",
        components: {
            footerBar
        },
        data() {
            return {
                isSearch:false,
                map:"杭州电子科技大学",
                orderDialogVisible:false,
                parkspace:[
                    {
                        content:'1号',
                        icon:"icon-seat",

                    },{
                        content:'2号',
                        icon:"icon-order",

                    },{
                        content:'3号',
                        icon:"icon-checked",

                    },{
                        content:'4号',
                        icon:"icon-checked",

                    }
                ]
            }
        },

        mounted(){
           this.search();
        },
        methods:{
            order:function(){
            this.orderDialogVisible = true;
            },
            search:function(){
                this.isSearch=true;
                // 百度地图API功能
                var map = new BMap.Map("nearBy");
                var point = new BMap.Point(116.331398,39.897445);
                map.centerAndZoom(point,12);
                // 创建地址解析器实例
                var myGeo = new BMap.Geocoder();
                // 将地址解析结果显示在地图上,并调整地图视野
                myGeo.getPoint(this.map, function(point){
                    if (point) {
                        map.centerAndZoom(point, 16);
                        map.addOverlay(new BMap.Marker(point));
                    }else{
                        console.log("您选择地址没有解析到结果!");
                    }
                }, "杭州市");
            },

        }

    }
</script>

<style scoped>
    .nearBy{
        width:100%;
        height:100%;
    }
    #nearBy{
        width:100%;
        height:100%;
        postion:absolute;
    }
    .footer{
        position:absolute;
        bottom:0px;
        width:100%;
    }
    header{
        position:absolute;
        top:0px;
        width:100%;
    }
    .icon {
        display: inline-block;
        width:50px;
        height:50px;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
        background-size: cover;
        margin-bottom:5px;
    }
    .icon-seat{
        background-image: url("../../images/park_seat.png");
    }
    .icon-order{
        background-image: url("../../images/park-order.png");
    }
    .icon-checked{
        background-image: url("../../images/parck-checked.png");
    }
    .parkspace{
        background-color: #ffffff;
        min-height:300px;
        width:80%;
        margin-left:10%;
        padding-top:10px;
        border-radius: 10px;
    }



</style>


