<template>
    <div class="order">
        <div class="unorder"v-show="!isShow"></div>
        <div class="ordered">
            <el-form  class="ordered-info" v-show="isShow" >
                <el-form-item v-show="!isStart">
                    <el-input style="width:200px" placeholder="请输入停车号"></el-input>
                </el-form-item>
                <el-form-item v-show="isStart" >
                   <div class="clock">
                       {{time}}
                   </div>
                    <p>
                        <el-button type="primary"v-show="!isPay"  @click="end()">结束</el-button>
                    </p>
                </el-form-item>
                <el-form-item v-show="isPay" >
                    <el-input style="width:200px" placeholder="请给个评价吧"></el-input>
                </el-form-item>
                <el-form-item v-show="isPay" >
                        <el-button type="primary" @click="pay()">立即支付</el-button>
                </el-form-item>
                <el-form-item v-show="!isStart">
                    <el-button type="primary" @click="isShow=false">取消预定</el-button>
                    <el-button type="primary" @click="open()">立即解锁车位</el-button>
                </el-form-item>
            </el-form>
        </div>
        <footer-bar class="footer"></footer-bar>
    </div>
</template>

<script>
    import footerBar from '../common/footerBar'
    export default {
        name: "order",
        components: {
            footerBar
        },
        data(){
            return{
                time:"00:00",
                isShow:true,
                timer:null,
                isPay:false,
                n:0,
                isStart:false
            }
        },
        methods:{
            open:function(){
                this.isStart=true;
                let $this=this;
                this.timer=setInterval(function () {
                    $this.n++;
                    var m=parseInt($this.n/60);
                    var s=parseInt($this.n%60);
                    $this.time=$this.toDub(m)+":"+$this.toDub(s);
                },1000/60);

            },
            end:function(){
                clearInterval(this.timer);
                this.n=0;
                this.isPay=true;
            },
            pay:function(){
                this.isShow=true;
                this.isStart=false;
                this.isPay=false;
            },
             toDub:function(n){
                    return n<10?"0"+n:""+n;
             }

        }
    }
</script>

<style scoped>
    .footer{
        width:100%;
        position: fixed;
        bottom:0px;
    }
    .ordered-info{
       margin-top:20px;
    }
    .clock{
        width:100px;
        height:100px;
        line-height:100px;
        border:1px solid gainsboro;
        border-radius: 50%;
        display:inline-block;
        margin-bottom:10px;

    }

</style>


