<template>
    <div class="check">
        <header>
            <i class="icon icon-nopay"></i>
            <p>未支付:10元</p>
            <el-button  type="warning"@click="isShow=true">立即支付</el-button>
        </header>
        <ul>
            <li v-for="(item,index) in list" :index="index">
                <i class="icon" :class="item.icon"></i>
                <div class="detail">
                    <p>
                        <span>{{item.detail}}</span>
                        <span class="value">{{item.value}}</span>
                    </p>
                    <p>{{item.date}}</p>
                </div>
            </li>
        </ul>
        <footer-bar class="footer"></footer-bar>
    </div>
</template>

<script>
    import footerBar from '../common/footerBar'
    export default {
        name: "check",
        components:{
            footerBar
        },
        data(){
            return{
                list:[
                    {  date:"2018-06-10 12:12:12",
                        detail:"杭电消费",
                       value:16,
                        icon:"icon-post"
                    },{
                        date:"2018-06-10 12:12:12",
                        detail:"充值",
                        value:18,
                        icon:"icon-get"
                    }
                ]

            }
        }
    }
</script>

<style lang="scss"scoped>
    header{
        background: #409eff;
        height:300px;
        margin-bottom: 20px;
        p{
            margin-top:30px;
            font-size:20px;
            color:#ffffff;
            margin-bottom: 20px;
        }
    }
    .check{
        padding-bottom: 50px;
    }
    .icon {
        display: inline-block;
        width:50px;
        height:50px;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
        background-size: cover;
        margin-bottom:5px;
    }
    .icon-get{
        background-image: url("../../images/icon-get.png");
    }
    .icon-post{
        background-image: url("../../images/icon-post.png");
    }
    .icon-nopay{
        background-image: url("../../images/icon-nopay.png");
        width:92px;
        height:65px;
        margin-top:20px;
    }
    li{ width:100%;
        text-align: left;
        margin-left:20px;
        border-bottom: 1px solid gainsboro;
    }
    .detail{
        display:inline-block;
        position:relative;
        top:-10px;
    }
    .footer{
        width:100%;
        position: fixed;
        bottom:0px;
    }


</style>


