<template>
    <div class="password">
        <el-form>
            <el-form-item  prop="name">
                <el-input placeholder="请输入账号" type="text"></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input placeholder="请输入密码"type="password"></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input placeholder="再次输入密码"type="password"></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-button type="primary" size="medium" @click="goTo()">确认修改</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "password",
        methods:{
            goTo:function(){
                this.$router.push("/login");
            }
        }
    }
</script>

<style scoped>
    .password{
        padding:20px;
    }

</style>


