<template>
    <div class="check">
        <ul>
            <li v-for="(item,index) in list" :index="index">
                <i class="icon" :class="item.icon"></i>
                <div class="detail">
                    <p>
                        <span>{{item.detail}}</span>
                    </p>
                    <p>{{item.date}}</p>
                </div>
            </li>
        </ul>
        <footer-bar class="footer"></footer-bar>
    </div>
</template>

<script>
    import footerBar from '../common/footerBar'
    export default {
        name: "comment",
        components:{
            footerBar
        },
        data(){
            return{
                list:[
                    {  date:"2018-06-10 12:12:12",
                        detail:"杭电停车真好",
                        value:16,
                        icon:"icon-comment"
                    },{
                        date:"2018-06-10 12:12:12",
                        detail:"啦啦啦啦",
                        value:18,
                        icon:"icon-comment"
                    }
                ]

            }
        }
    }
</script>

<style lang="scss" scoped>
    .icon {
        display: inline-block;
        width:50px;
        height:50px;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
        background-size: cover;
        margin-bottom:5px;
    }
    li{ width:100%;
        text-align: left;
        margin-left:20px;
        border-bottom: 1px solid gainsboro;
    }
    .detail{
        display:inline-block;
        position:relative;
        top:-10px;
    }
    .icon-comment{
        background-image: url("../../images/icon-comment.png");
    }
    .footer{
        width:100%;
        position: fixed;
        bottom:0px;
    }

</style>


