<template>
  <div class="login">
    <el-form :model="form" ref="loginForm" label-width="50px" class="login-form">
      <el-form-item>
        <span class="form-title">惠停车</span>
      </el-form-item>
      <el-form-item  prop="name">
        <el-input placeholder="请输入账号" v-model="form.name" type="text"></el-input>
      </el-form-item>
      <el-form-item  prop="password">
        <el-input placeholder="请输入密码" v-model="form.password"type="password"></el-input>
      </el-form-item>
      <el-form-item  prop="password">
        <el-input placeholder="姓名" v-model="form.password"type="password"></el-input>
      </el-form-item>
      <el-form-item  prop="password">
        <el-input placeholder="手机号码" v-model="form.password"type="password"></el-input>
      </el-form-item>
        <el-button type="primary" size="medium" @click="submitForm('loginForm')">注册</el-button>
        <p class="button-login" @click="gotoLogin">已注册，去登录>></p>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          password: '',
          category: 0,
        },
      };
    },
    mounted: function() {
    },
    methods: {
      //找回密码
      userFind:function(){
        this.$router.push('/user-passwordFind');
      },
      //登录
      submitForm(formName) {
        var $this=this;
        if(!(this.form.name&&this.form.password)){
          var  h = this.$createElement;
          this.$notify({
            title: '',
            message: h('i', { style: 'color: teal'}, '账户或密码为空'),
            type: 'warning',
            position: 'right-bottom',
            offset: 300,
            duration:1000
          });
          return 0;
        }

        this.$refs[formName].validate((valid) => {
            this.$router.push('/index');
        });
      },
        gotoLogin:function(){
            this.$router.push('/login');
        }

    }
  }
</script>
<style lang="scss" scoped>
 .login {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    opacity: 80;
    -moz-opacity: 0.8;
   background: #324157;
   overflow: hidden;
   position:relative;
  }
  .login-form{
    width:100%;
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%,-50%);
    background: white;
    padding:30px 40px 20px 20px;
    text-align: center;
    color: #409EFF;
      .form-title{
          font-size:16px;
      }
      .button-login{
          font-size:12px;
          text-align: right;
          cursor:pointer;
      }
  }

</style>



