<template>
    <div class="recharge">
        <header>
            <i class="icon icon-money"></i>
            <p>余额:999999元</p>
            <el-button  type="warning"@click="isShow=true">充值</el-button>
        </header>
        <el-form  class="add-info" v-show="isShow==true" >
            <el-form-item>
                <el-input style="width:200px" placeholder="金额"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="isShow=false;">取消</el-button>
                <el-button type="primary" @click="isShow=false;">确认充值</el-button>
            </el-form-item>

        </el-form>

        <footer-bar class="footer"></footer-bar>
    </div>
</template>

<script>
    import footerBar from '../common/footerBar'
    export default {
        name: "recharge",
        components:{
            footerBar
        },
        data(){
           return {
               isShow:"false"
           }
        }
    }
</script>

<style lang="scss"scoped>

.footer{
    position:fixed;
    width:100%;
    bottom:0px;
}
header{
    background: #409eff;
    height:300px;
    margin-bottom: 20px;
    p{
        margin-top:30px;
        font-size:20px;
        color:#ffffff;
        margin-bottom: 20px;
    }
}
.icon {
    display: inline-block;
    width:50px;
    height:50px;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
    background-size: cover;
    margin-bottom:5px;
}
.icon-money{
    margin-top:10px;
    background-image: url("../../images/icon-money.png");
}
</style>


